.content {
    display: flex;
    width: 100%;
    background: linear-gradient(50deg, var(--color-secondary-50) 40%, var(--color-primary) 80%);
    color: var(--color-neutral);
    border-radius: var(--spacing-lg) 0 0 var(--spacing-lg);
    opacity: 0.95;
    min-height: 80vh;
    overflow: hidden;
    
    &__left-part {
      flex: 4;
      display: flex;
      background: linear-gradient(270deg, var(--color-secondary-50) 0%, var(--color-secondary) 20%);
      border-radius: var(--spacing-lg) 0 0 var(--spacing-lg);
      position: relative;
      overflow: hidden;
    
      &-image,
      &-video {
        flex: 1;
        height: 100%;
        width: 30%;
        background-size: cover;
        background-position: center;
        border-radius: var(--spacing-lg) 0 0 var(--spacing-lg);
        animation: fadeInBlur 1.2s ease-in-out;
        transition: transform 0.6s ease-in-out, filter 0.6s ease-in-out;
      }
      
      &-image-carousel {
        width: 30%;
        height: 100%;
        border-radius: var(--spacing-lg) 0 0 var(--spacing-lg);
        overflow: hidden;
      }
    
      &__content {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: var(--spacing-lg) var(--spacing-md);
        justify-content: center;
    
        h2 {
          font-size: 2.2rem;
          font-weight: 700;
          color: var(--color-primary);
          padding-bottom: var(--spacing-md);
          transition: color 0.3s ease-in-out;
    
          &:hover {
            color: var(--color-accent);
          }
        }
    
        &__description {
          font-size: 1rem;
          font-weight: 500;
          padding-bottom: var(--spacing-lg);
          line-height: 1.5;
          color: var(--color-neutral-80);
        }
    
        &-header {
          padding: var(--spacing-md) 0;
        }
    
        &-list {
          display: flex;
          flex-direction: column;
          gap: var(--spacing-md);
    
          li {
            display: flex;
            align-items: center;
            gap: var(--spacing-sm);
            padding-left: 32px;
            position: relative;
            transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;
          }
        }
      }
    }
    
    &__paw {
      position: absolute;
      left: 0;
      top: 0;
      color: var(--color-primary);
      background-color: var(--color-accent);
      padding: 4px;
      border-radius: 50%;
      height: 16px;
      width: 16px;
    }
    
    &__right-part {
      flex: 1;
      display: flex;
      flex-direction: column;
      margin: 0;
      z-index: 2;
      justify-content: space-between;
      padding: var(--spacing-xl) 0;
    
      &-logo {
        width: 140px;
        height: 140px;
        color: white;
        align-self: center;
      }
    
      &-list {
        display: flex;
        flex: 1;
        gap: 2px;
        flex-direction: column;
        justify-content: center;
        padding: 0 var(--spacing-md);
      }
    
      &-item {
        border: 2px solid var(--color-secondary);
        padding: var(--spacing-md) 0;
        text-align: center;
        color: white;
        transition: all 0.3s ease-in-out;
        font-weight: 500;
        border-radius: var(--spacing-sm);
        background: rgba(255, 255, 255, 0.05);
        cursor: pointer;
        position: relative;
    
        &:hover {
          background-color: var(--color-accent);
          color: var(--color-neutral);
        }
    
        &--active {
          background-color: var(--color-secondary);
          color: var(--color-accent);
          font-weight: bold;
          box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
          transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
        }
      }
    }
  }
    
  @keyframes fadeInBlur {
    0% {
      opacity: 0;
      filter: blur(20px);
    }
    
    100% {
      opacity: 1;
      filter: blur(0px);
    }
  }